.post-enter {
	transform: translateX(0px);

}
.post-enter-active {
	transform: translateX(0px);
	transition: all 500ms ease-in;
}
.post-exit {
	opacity: 1;
}
.post-exit-active {
	transform: translateX(-350px);
	transition: all 500ms ease-in;
}


@media (max-width:767px) {
	.App {
		max-width: 95%;
	}
}