.comments{
	margin-top: 10px;
	display: none;
	background-color: rgba(122, 22, 252, 0.174);
	border-radius: 10px;
	padding: 10px;
	box-sizing: border-box;
	position: relative;
}
.comments.active{

	position: relative;
	z-index: 1;
	// border-top: 2px solid hsl(223, 19%, 93%);
	
	// padding-right: 10px;
	display: flex;
	height: auto;
	flex-direction: column;
	animation-name: slide, opacitier, blockheigth;
	animation-duration: 1s, 1s, 1s;
	animation-timing-function: linear, linear, linear;
}

@keyframes slide {
	0%{	
		transform: translateY(-300px);
	}
	100%{
		transform: translateY(0px);
	}
}
@keyframes opacitier {
	0%{	
		opacity:0;
	}
	50%{
		opacity:0;
	}
	100%{
		opacity:1;
	}
}
