$LightGray: hsl(223, 19%, 93%);
$LightGrayishBlue: hsl(238, 48%, 88%);
.navbar{
	z-index: 30;
	top: 0;
	// transform: translateZ(0);
	// -webkit-transform: translateZ(0);
	border-radius: 15px;
	position: fixed;
	box-sizing: border-box;
	width: 100vw;
	display: flex;
	padding: 0px 25vw;
	justify-content: space-between;
	align-items: center;
	background-color: rgba($LightGrayishBlue, 1);
	box-shadow:  -1px -2px 20px rgba($LightGrayishBlue, 0.3) inset,
	 1px 2px 20px rgba($LightGrayishBlue, 0.3) inset,
	-1px 5px 42px 0px rgba($LightGrayishBlue, 0.8);
	margin: 0 auto;
	padding: 0 20vw;
	height: 60px;
 	// padding: 0;
}
.links{
	width: 65%;
	display: flex;
	justify-content: space-between;
	align-items: center;

}
.auth{
	width: 25%;
}
.image{
	max-height: 80%;
}
.btn{
	background-color: rgba($color: #000000, $alpha: 0);

}